import caseReducer from './caseReducer'

const common = {
  httpFailureResponse: {},
  httpFailureHistory: [],
  showModal: true
}

function httpFailure(state, action) {
  const httpFailureHistory = state.httpFailureHistory.slice(0, state.httpFailureHistory.length+1)
  httpFailureHistory.push(action)
  return {...state, httpFailureResponse:action.response, httpFailureHistory}
}

function clearHttpFailure(state) {
  return {...state, httpFailureResponse:{}, httpFailureHistory:[]}
}

function closeModal (state) {
  return {...state, showModal: false}
}

function openModal (state) {
  return {...state, showModal: true}
}

export default caseReducer(common, {
  HTTP_FAILURE: httpFailure,
  CLEAR_HTTP_FAILURE: clearHttpFailure,
  CLOSE_MODAL_SUCCESS: closeModal,
  OPEN_MODAL_SUCCESS: openModal
})



